.articles {
  min-width: 100%;
  padding: 8rem 0;
  display: flex;
}
.articles .container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.articles .container .section-title {
  margin-bottom: 3.5rem;
}
@media (min-width: 768px) {
  .articles .container .section-title {
    margin-bottom: 8rem;
  }
}
.articles__loop .article {
  padding: 5rem 0;
}
.articles__loop .article__wrapper {
  max-width: 100%;
  padding: 5rem 1.8rem 2rem 1.8rem;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 1.5rem;
  background-color: transparent;
  border: 1px solid rgba(var(--primary-color), 1);
  border-top: none;
}
@media (min-width: 1024px) {
  .articles__loop .article__wrapper {
    padding-top: 7.5rem;
  }
}
.articles__loop .article__wrapper::after, .articles__loop .article__wrapper::before {
  content: "";
  display: inline-block;
  width: 10%;
  border-top: 1px solid rgba(var(--primary-color));
}
.articles__loop .article__wrapper::after {
  position: absolute;
  top: 0;
  right: 0;
}
.articles__loop .article__wrapper::before {
  position: absolute;
  top: 0;
  left: 0;
}
.articles__loop .article__title {
  width: 80%;
  position: absolute;
  top: -2.5rem;
  left: 10%;
  display: block;
}
@media (min-width: 768px) {
  .articles__loop .article__title {
    top: -4.5rem;
  }
  .articles__loop .article__title a {
    height: 6.8rem;
    font-size: 2.5rem;
    line-height: 3rem;
  }
}
@media (min-width: 1440px) {
  .articles__loop .article__title {
    top: -3rem;
  }
}
.articles__loop .article__title a {
  display: block;
  font-family: var(--lato);
  font-weight: 700;
  font-size: 2rem;
  line-height: 2.2rem;
  text-align: center;
  text-transform: uppercase;
  color: rgba(var(--text-color), 1);
  transition: 0.3s ease-in-out;
}
.articles__loop .article__title a:hover {
  color: rgba(var(--secondary-color), 1);
}
.articles__loop .article__image {
  max-width: 100%;
  width: 100%;
  height: 15rem;
  margin-bottom: 30px;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (min-width: 1024px) {
  .articles__loop .article__image {
    height: 20rem;
  }
}
.articles__loop .article__image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: 0.3s ease;
}
.articles__loop .article__image:hover img {
  transform: scale(1.5);
}
.articles__loop .article__date {
  font-family: var(--lato);
  font-weight: 700;
  font-size: 1.8rem;
  line-height: 2rem;
  text-align: center;
  text-transform: capitalize;
  color: rgba(var(--primary-color), 1);
}
@media (min-width: 1024px) {
  .articles__loop .article__date {
    font-size: 2rem;
    line-height: 2.2rem;
  }
}
.articles__loop .article__author {
  font-family: var(--lato);
  font-size: 1.4rem;
  font-weight: 700;
  line-height: 1.6rem;
  text-align: center;
  text-transform: capitalize;
  color: rgba(var(--text-color), 1);
}
.articles__loop .article__link {
  font-family: var(--lato);
  font-size: 1.6rem;
  line-height: 2rem;
  text-align: center;
}

.blog__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.blog__wrapper .articles {
  padding: 0;
}
.blog__wrapper .articles__loop {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  gap: 3rem;
}
@media (min-width: 768px) {
  .blog__wrapper {
    display: grid;
    grid-template-columns: 65% 30%;
    justify-content: space-between;
    align-content: center;
    align-items: start;
    justify-items: center;
  }
  .blog__wrapper .articles__loop {
    display: grid;
    grid-template-columns: repeat(2, 46%);
    justify-content: space-between;
    align-content: center;
    align-items: center;
    justify-items: center;
  }
}
@media (min-width: 1440px) {
  .blog__wrapper .articles__loop {
    grid-template-columns: repeat(3, 30%);
  }
}